<template>
	<div id="detpMenage">
		<div class="deptInfo">
			<h3>部门信息</h3>
			<p>
				部门总数：<span class="tips">{{empCount}}</span>
			</p>
			<p><el-button type="primary" round @click="dempAddDialog=true">增加部门</el-button></p>
			<div>
				<el-table :data="tableData">
					  <el-table-column prop="deptName" label="部门名称" width="180">
					  </el-table-column>
					  <el-table-column prop="deptNumber" label="部门人数"  width="180">
					  </el-table-column>
					  <el-table-column prop="superior" label="部门负责人"  width="180">
					  </el-table-column>
					  <el-table-column label="操作" >
						  <template slot-scope="scope" >
							<el-button type="text" size="small" @click="updateDeptInfo(scope.row)">编辑</el-button>
						  </template>
						</el-table-column>
					</el-table>
			</div>
		</div>
		
		<!-- 部门增加 -->
		<div>
			<el-dialog title="增加部门信息" :visible.sync="dempAddDialog" width="600px">
				<el-form ref="formUpdata" :model="formUpdata" label-width="85px">
					<el-form-item label="部门名称">
						<el-input v-model="formUpdata.deptName" style="width: 220px;"></el-input>
					</el-form-item> 
					<el-form-item label="部门负责人">
						<el-select v-model="formUpdata.superior" placeholder="部门负责人">
							<el-option  v-for=" item in empList" :label="item.name" :value="item.id" :key="item.id"></el-option>
						</el-select>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="addDept">确 定</el-button>
					<el-button @click="dempAddDialog = false">取 消</el-button>
				</div>
			</el-dialog>
		</div>
		<!-- end -->
		
		<!-- 员工修改 -->
		<div>
			<el-dialog title="修改部门信息" :visible.sync="updateDialog" width="600px">
				<el-form ref="formUpdata" :model="formUpdata" label-width="85px">
					<el-form-item label="部门名称">
						<el-input v-model="formUpdata.deptName" style="width: 220px;"></el-input>
					</el-form-item> 
					<el-form-item label="部门负责人">
						<el-select v-model="formUpdata.superior" placeholder="部门负责人">
							<el-option  v-for=" item in empList" :label="item.name" :value="item.id" :key="item.id"></el-option>
						</el-select>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button type="primary" @click="addDept">确 定</el-button>
					<el-button @click="updateDialog = false">取 消</el-button>
				</div>
			</el-dialog>
		</div>
		<!-- end -->
	</div>
</template>

<script>
	export default {
		data(){
			return {
				dempAddDialog:false,
				updateDialog:false,
				
				empCount:'', //总人数
				
				//存储部门信息
				tableData:[],
				
				deptList:[], //存储部门信息
				
				empList:[], //上司
				
				//存储部门修改信息
				formUpdata:{
					deptName:"",
					id:'',
					superior:''
				},
				
			
				
			}
		},
		mounted(){
			//查询部门信息
			this.quaryDeptAll()
			//查询部门
			this.getDetpAll();
			
			//查询所有员工信息
			this.getdetpSuperior();
		},
		methods:{
			
			//增加
			addDept(){
				this.dempAddDialog = false
				this.$http.post("deptUIServe",{
						method:"deptAdd",
						detpList:JSON.stringify(this.formUpdata)
					}).then(res =>{
						var _this = this
						//修改成功后 刷新页面
						this.quaryDeptAll()
						this.$message({
							type: 'success',
							message: res.msg,
							onClose:()=>{
								_this.formUpdata = {}
							}
						})
					})
			},
			//查询部们信息
			quaryDeptAll(){
				this.$http.get("deptUIServe").then(res=>{
					if (res.code) {//code有值，说明存在问题
						this.$message({
				          message: res.msg,
				          type: 'warning'
				        });
					} else {
						//部门统计信息·
						this.tableData = res.dataVo.deptV;
						
						//获取总人数
						this.empCount = res.dataVo.empCount
					}
				});
			},
			//修改
			updateDeptInfo(row){
				console.log(row)
				this.updateDialog = true;
				//获取要修改的数据
				this.formUpdata=row;
			},
			//提交修改
			updataDept(){
			
				this.updateDialog = false;
				for (var i = 0; i < this.empList.length; i++) {
					if(this.formUpdata.superior== this.empList[i].name){
						this.formUpdata.superior = this.empList[i].id;
					}
				}
				this.$http.post("deptUIServe",{
						method:"detpUpate",
						detpList:JSON.stringify(this.formUpdata)
					}).then(res =>{
						var _this = this
						//修改成功后 刷新页面
						this.quaryDeptAll()
						this.$message({
							type: 'success',
							message: res.msg,
							onClose:()=>{
								_this.formUpdata = {}
							}
						})
					})
			},
			//查询部门
			getDetpAll(){
				this.$http.get("empServe",{
						method:"queryDetp"
					}).then(res =>{
						this.deptList =res.dataVo
					})
			},
			//获取所有员工信息
			getdetpSuperior(){
				//queryAll
				this.$http.get("empServe",{
						method:"queryAll"
					}).then(res =>{
						this.empList =res.dataVo
					})
			}
		}
	}
</script>

<style scoped>
	.deptInfo{
		margin-left: 20px;
	}
</style>



