import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
	userId: sessionStorage.userId || "",
	userIphone: sessionStorage.userIphone || "",
	userName: sessionStorage.userName || "",
	userType:sessionStorage.userType || "0"

}
const mutations = {
	changeuserId (state,userId) {
		state.userId = userId;
		//存入本地session缓存中
		sessionStorage.userId = userId;
	},
	changuserIphone (state,userIphone) {
		state.userIphone = userIphone;
		sessionStorage.userIphone = userIphone;
	},
	changeuserName (state,userName) {
		state.userName = userName;
		sessionStorage.userName = userName;
	},
	changeuserType (state,userType) {
		state.userType = userType;
		sessionStorage.userType = userType;
	},
	clearUserInfo () {
		state.userId = "";
		sessionStorage.userId = "";

		state.userIphone = "";
		sessionStorage.userIphone = "";

		state.userName = "";
		sessionStorage.userName = "";

		state.userType = "";
		sessionStorage.userType = "";
	}
}

export default new Vuex.Store({
	state:state,
	mutations:mutations
});




