import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
//登录
import Login from '@/components/Login'
import Admin from '@/components/admin/Admin.vue'
//首页
import index from '@/components/admin/pages/index.vue'
//员工信息
import EmpList from '@/components/admin/pages/empList'
//员工增加
import AddEmp from '@/components/admin/pages/addEmp'
//考勤查询
import Checking from '@/components/admin/pages/checking'
//部门管理
import DetpManage from '@/components/admin/pages/detpManage'
//考勤设置
import SystConfig from '@/components/admin/pages/systemConfig'

import store from '@/vuex/store'

//在Vue中加入router组件
Vue.use(Router)

const  router =  new Router({
	base:"/psCompany_system/", //打包必备
  routes: [

		{
		  path: '/',
		  name: 'HelloWorld',
		  component: Login
		},
		{
			//登录
		  path: '/Login',
		  name: 'Login',
		  component: Login
		},
		{
			//管理员主页面
		  path: '/Admin',
		  name: 'Admin',
		  component: Admin,
			children:[
				{
					path:'index',
					name:'adminIndex',
					component:index,
					// 需要登录才能进入的页面可以增加一个meta属性
					meta:{
						requireAuth: true
					},
				},
				{
					path:'empList',
					name:'adminempList',
					component:EmpList,
					// 需要登录才能进入的页面可以增加一个meta属性
					meta:{
						requireAuth: true
					},
				},
				{
					path:'empList',
					name:'adminempList',
					component:EmpList,
					// 需要登录才能进入的页面可以增加一个meta属性
					meta:{
						requireAuth: true
					},
				},
				{
					path:'addEmp',
					name:'adminAddEmp',
					component:AddEmp,
					// 需要登录才能进入的页面可以增加一个meta属性
					meta:{
						requireAuth: true
					},
				},
				{
					path:'checking',
					name:'adminChecking',
					component:Checking,
					// 需要登录才能进入的页面可以增加一个meta属性
					meta:{
						requireAuth: true
					},
				},
				{
					path:'detpManage',
					name:'adminDetpManage',
					component:DetpManage,
					// 需要登录才能进入的页面可以增加一个meta属性
					meta:{
						requireAuth: true
					},
				},
				{
					path:'systConfig',
					name:'adminSystConfig',
					component:SystConfig,
					// 需要登录才能进入的页面可以增加一个meta属性
					meta:{
						requireAuth: true
					},
				},
			]
		}
  ]
});
//路由转发之前
router.beforeEach((to, from, next) => {
    
    // 判断是否需要登录权限
    if(to.matched.some(res => res.meta.requireAuth)){
        //是否已经登录
        if(!store.state.userId){ 
          //redirect: Router.currentRoute.fullPath
            router.push({
                path:"/login",
                query: {redirect: to.fullPath}
            });
        }else{
            next()
        }
    }else{
        next()
    }
    
})

export default router



