<template>
	<div id="addEmp">
			<h3 class="h-c">员工新增</h3>
			<div class="m-a">
				<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
					<el-form-item label="员工姓名" prop="name" ><el-input v-model="ruleForm.name" placeholder="请输入名称" style="width:250px;"></el-input></el-form-item>
					<el-form-item label="手机号" prop="ipone"><el-input v-model="ruleForm.ipone" placeholder="请输入手机号" style="width:250px;"></el-input></el-form-item>
					<el-form-item label="密码" prop="password"><el-input v-model="ruleForm.password" placeholder="请输入密码" style="width:250px;"></el-input></el-form-item>
					<el-form-item label="薪资" prop="pay" ><el-input v-model="ruleForm.pay" placeholder="请输入薪资" style="width:250px;"></el-input></el-form-item>
					<el-form-item label="部门" prop="department_id">
						<el-select v-model="ruleForm.department_id" placeholder="请选择部门">
							<el-option v-for="item in deptList" :label="item.deptName" :value="item.id" :key="item.id"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="入职时间" required>
						<el-col :span="11">
							<el-form-item prop="entry_time">
								<el-date-picker  type="date"  placeholder="选择日期" v-model="ruleForm.entry_time" style="width: 100%;"></el-date-picker>
							</el-form-item>
						</el-col>
					</el-form-item>
					<el-form-item label="性别" prop="sex">
						<el-radio-group v-model="ruleForm.sex">
							<el-radio label="男">男</el-radio>
							<el-radio label="女">女</el-radio>
						</el-radio-group>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
						<el-button @click="resetForm('ruleForm')">重置</el-button>
					</el-form-item>
				</el-form>
			</div>
			<div>
					<el-dropdown>
						<i class="el-icon-setting" style="margin-right: 15px;font-size: 12px">
							<el-button type="text" @click="returnToPage">返回首页</el-button>
						</i>
					</el-dropdown>
			</div>
	</div>
</template>

<script>
	
	var moment = require('moment');
	
	export default {
		data() {
			return {
				deptList: [], //存储部门信息

				ruleForm: {
					department_id: '',
					entry_time: '',
					ipone: '',
					name: '',
					pay: '',
					sex: '',
					state: '',
					password:''
				},
				rules: {
					name: [{ required: true, message: '请输入名称!', trigger: 'blur' }],
					ipone: [{ required: true, message: '请输入手机号!', trigger: 'blur' }],
					pay: [{ required: true, message: '请输入你的薪资!', trigger: 'blur' }],
					department_id: [{ required: true, message: '请选择部门!', trigger: 'change' }],
					sex: [{ required: true, message: '请填写性别!', trigger: 'change' }],
					password:[{ required: true, message: '请输入密码!', trigger: 'blur' }]
				}
			};
		},
		mounted() {
			//查询部门信息
			this.getDetpAll();
		},
		methods: {
			
			//提交
			submitForm(formName) {
				this.$refs[formName].validate(valid => {
					if (valid) {
							//格式化日期  并且转为字符串
							var  dataTime =moment(this.ruleForm.entry_time).format('YYYY-MM-DD')  
							this.ruleForm.entry_time = dataTime;
							//console.log(this.ruleForm)
							this.$http.post("empServe",{
									method:"empAdd",
									empList:JSON.stringify(this.ruleForm)
							}).then(res=>{
								let _this = this;
										this.$message({
												type: 'success',
												message:res.msg ,
												onClose:()=>{
														_this.$router.replace({
															path:"/admin/empList"
														})
												}
										})
							}).catch(err=>{
									this.$message({
									  message: res.msg,
									  type: 'error',
									});
							})
							
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			//重置
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			//查询部门
			getDetpAll() {
				this.$http
					.get('empServe', {
						method: 'queryDetp'
					})
					.then(res => {
						this.deptList = res.dataVo;
					});
			},
			returnToPage(){
						this.$router.replace({
						path:"/admin/index"
					})
			}
		}
	};
</script>

<style>
.m-a {
	width: 600px;
	margin: 0px auto;
		
	margin-top: 30px;
}
.h-c {
	color: #b3c0d1;
	text-align: center;
	margin-top: 30px;
	padding-right: 199px;
}
</style>



