<template>
	<div id="emplist">
		<el-form :inline="true" :model="formInline" class="demo-form-inline el-f" ref="searchForm">
			<el-form-item label="员工姓名" prop="user"><el-input v-model="formInline.user" placeholder="员工姓名"></el-input></el-form-item>

			<el-form-item label="状态" prop="state">
				<el-select v-model="formInline.state" placeholder="专题">
					<el-option label="试用期" value="1"></el-option>
					<el-option label="正式员工" value="2"></el-option>
					<el-option label="离职" value="3"></el-option>
					<el-option label="删除" value="0"></el-option>
				</el-select>
			</el-form-item>

			<el-form-item label="部门" prop="region">
				<el-select v-model="formInline.region" placeholder="部门名称">
					<el-option  v-for=" item in deptList" :label="item.deptName" :value="item.id" :key="item.id"></el-option>
				</el-select>
			</el-form-item>

			<el-form-item>
				<el-button type="primary" @click="onSearch">搜索</el-button>
				<el-button @click="resetForm('searchForm')">重置</el-button>
			</el-form-item>
		</el-form>
		<el-main>
			<el-table :data="tableData">
				<el-table-column prop="name" label="姓名" width="120"></el-table-column>
				<el-table-column prop="ipone" label="手机号" width="150"></el-table-column>
				<el-table-column prop="sex" label="性别" width="120"></el-table-column>
				<el-table-column prop="pay" label="薪资" width="120"></el-table-column>
				<el-table-column prop="entry_time" label="入职时间" width="120"></el-table-column>
				<el-table-column prop="state" label="状态" width="120"></el-table-column>
				<el-table-column prop="department_name" label="部门名称" width="120"></el-table-column>
				<el-table-column prop="superior" label="上司" width="100"></el-table-column>
				<el-table-column prop="operation" label="操作" width="150">
					<template slot-scope="scope">
						<el-button type="primary" icon="el-icon-edit" @click="onEmpUpateInfo(scope.row)"></el-button>
						<el-button type="primary" icon="el-icon-delete" @click="onDelete(scope.row)"></el-button>
					</template>
				</el-table-column>
			</el-table>
		</el-main>
		<!-- 分页 -->
		<div class="block" style="text-align: right;">
			<el-pagination
				@size-change="handleSizeChange"
				@current-change="handleCurrentChange"
				:current-page.sync="currentPage"
				:page-sizes="[5, 10, 20, 50]"
				:page-size="SizeChange"
				layout="total, sizes, prev, pager, next, jumper"
				:total="total"
			></el-pagination>
		</div>
		<!-- end -->

		<!-- 员工修改 -->
		<el-dialog title="修改员工信息" :visible.sync="dialogFormVisible" width="600px">
			<el-form ref="form" :model="form" label-width="80px">
				<el-form-item label="员工名称"><el-input v-model="form.name"></el-input></el-form-item>
				<el-form-item label="手机号"><el-input v-model="form.ipone"></el-input></el-form-item>
				<el-form-item label="薪资"><el-input v-model="form.pay"></el-input></el-form-item>
				<el-form-item label="部门">
					<el-select v-model="form.department_id" placeholder="请选择部门">
						<el-option  v-for=" item in deptList" :label="item.deptName" :value="item.id" :key="item.id"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="普及">
					<el-select v-model="form.state" placeholder="请选择普及">
						<el-option label="试用期" value="1"></el-option>
						<el-option label="正式员工" value="2"></el-option>
						<el-option label="离职" value="3"></el-option>
						<el-option label="删除" value="0"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="入职时间">
					<el-col :span="11"><el-date-picker type="date" placeholder="选择日期" v-model="form.entry_time" style="width: 100%;"></el-date-picker></el-col>
					
				</el-form-item>
				<el-form-item label="性别">
					<el-radio-group v-model="form.sex">
						<el-radio label="男" >男</el-radio>
						<el-radio label="女" >女</el-radio>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button type="primary" @click="onEmpUpate">确 定</el-button>
				<el-button @click="dialogFormVisible = false">取 消</el-button>
			</div>
		</el-dialog>
		<!-- end -->
	</div>
</template>

<script>
export default {
	data() {
		return {
			//员工数据
			tableData: [],
			//当前页数
			currentPage: 1,
			//展示多少条记录
			SizeChange:5,
			//总页数
			total:0,

			//存储模糊查询的信息
			formInline: {
				user: '',
				state: '',
				region: ''
			},
			deptList:[], //存储部门信息
			dialogFormVisible: false,
			
			//存储修改的
			form: {
				 department_id:'',
				 department_name: "",
				 entry_time: "",
				 id: "",
				 ipone: "",
				 name: "",
				 password: "",
				 pay:"",
				 sex: "",
				 state: "",
				 superior: "",
				 type: "" 
			},
			flag:true
		};
	},
	//钩子函数
	mounted(){
		//查询员工信息
		this.getEmpQueryAll();
		//部门信息
		this.getDetpAll()
	},
	methods: {
		
		//每页 多少展示条记录 
		handleSizeChange(val) {
			this.SizeChange = val;
			if(this.flag){
				this.getEmpQueryAll();
			}else{
				this.onSearch()
			}
			
		},
		//当前页数
		handleCurrentChange(val) {
			this.currentPage = val;
			if(this.flag){
				this.getEmpQueryAll();
			}else{
				this.onSearch()
			}
		},
		//end 
		
		//查询员工信息
		getEmpQueryAll(){
			this.$http.get("empServe",{
					method:"empQueryAll",
					page:this.currentPage,
					row:this.SizeChange
				}).then(res =>{
				this.tableData = res.dataVo;
				this.total = res.total;
			})
		},

		//搜索
		onSearch() {
			this.flag = false
			this.$http.get("empServe",{
					method:"queryVague",
					page:this.currentPage,
					row:this.SizeChange,
					empName:this.formInline.user,
					state:this.formInline.state,
					detpId:this.formInline.region 
				}).then(res =>{
					this.tableData =res.dataVo;
					this.total = res.total;
				})
			
		},
			
		//重置
		 resetForm(formName) {
			this.isSearch = false;
			this.$refs[formName].resetFields();
		  },
		
		//修改
		onEmpUpateInfo(row) {
			this.dialogFormVisible = true;
			this.form = row;
			console.log('修改');
		},
		//将要修改的数据提交
		onEmpUpate(){
			//var f =this.form
			this.$http.post("empServe",{
					method:"empUpate",
					empList:this.form
				}).then(res =>{
					let _this = this
					if(res.msg=="修改失败!"){
						this.$message({
						  message: res.msg,
						  type: 'error',
						  
						});
					}else{
						this.$message({
						  message: res.msg,
						  type: 'success',
						  onClose:()=>{
							  _this.getEmpQueryAll();
						  }
						});
					}
				})
				this.dialogFormVisible = false
				//debugger;
		},
		//删除
		onDelete(row) {

			 this.$confirm('你确定要删除'+row.name+'吗?', '提示', {
				  confirmButtonText: '确定',
				  cancelButtonText: '取消',
				  type: 'warning'
				}).then(() => {
				// 发送请求
				  this.$http.post("empServe",{
						method:"empDelete",
						empList:JSON.stringify(row)
					}).then(res =>{
						
						if(res.code){
							this.$message({
							  message: res.msg,
							  type: 'error',
							});
						}else{
							this.getEmpQueryAll();
							this.$message({
								type: 'success',
								message: '删除成功!',
							})
						}
					})
				  
				}).catch(() => {
				  this.$message({
					type: 'info',
					message: '已取消删除'
				  });          
				});
		},
		
		//查询部门
		getDetpAll(){
			this.$http.get("empServe",{
					method:"queryDetp"
				}).then(res =>{
					this.deptList =res.dataVo
				})
		}
	}
};
</script>

<style scoped>
.el-header {
	background-color: #b3c0d1;
	color: #333;
	line-height: 15%;
}

.el-aside {
	color: #333;
}
.el-f {
	margin-top: 15px;
	margin-left: 29px;
}
</style>



