<template>
	<div id="index">
		<div  class="emp-info">
			<h2>公司总览</h2>
			<div>
				<h3>员工信息</h3>
				<p>
					员工总数：<span class="tips">{{empCount}}</span>，总薪资：<span class="tips">{{sumSalary}}</span>，平均薪资：<span class="tips">{{avgSalary}}</span>
				</p>
			</div>
			<div id="echCy" style="width: 100%;height:400px;">
				
			</div>
		</div>
	</div>
</template>
 
<script>
	var echarts = require('echarts');
	export default {
		
		data(){
			return {
				avgSalary:"",
				empCount:"",
				sumSalary:""
			}
		},
		//钩子函数
		mounted(){
			this.$http.get("deptUIServe").then(res=>{
				if (res.code) {//code有值，说明存在问题
					this.$message({
			          message: res.msg,
			          type: 'warning'
			        });
				} else {
					//debugger
					this.avgSalary= parseInt(res.dataVo.avgSalary).toFixed(1);
					this.empCount = res.dataVo.empCount;
					this.sumSalary = parseInt(res.dataVo.sumSalary).toFixed(1);

					this.cylindricity(res.dataVo.deptV);
					//this.initDeptInfoChars( res.data.depts);
				}
			});
			
		},
		methods:{
			
			cylindricity(deptV){
				
				// 基于准备好的dom，初始化echarts实例
				var myChart = echarts.init(document.getElementById('echCy'));
				
				//获取x轴(部门名称)
				let deptNames = [];
				//y轴  部门人数
				let deptCount = [];
				
				//{deptName: "总裁部", superior: "张毅", deptNumber: "1"}
				
				for (var i = 0; i < deptV.length; i++) {
					deptNames.push(deptV[i].deptName);
					deptCount.push(deptV[i].deptNumber);
				}
				
				 // 指定图表的配置项和数据
				var option = {
					color: ['#3398DB'],
					 tooltip : {
						trigger: 'axis',
						axisPointer : {      // 坐标轴指示器，坐标轴触发有效
							type : 'shadow'   // 默认为直线，可选为：'line' | 'shadow'
						}
					},
					grid: {
					
						containLabel: true
					},
					xAxis: {
						type : 'category',
						name: '部门',
						data: deptNames,
						axisTick: {
							alignWithLabel: true
						}
					},
					yAxis: {
						name: '员工数量',
						type: 'value',
						
					},
					series: [{
						name:"员工人数",
						type: 'bar',
						barWidth: '60%',
						data: deptCount,
						
					}]
				};
				 // 使用刚指定的配置项和数据显示图表。
				myChart.setOption(option);
			}
			
		}
	}
</script>

<style scoped>
	.emp-info{
		margin-left: 20px;
	}
	.tips{
		
		font-size:22px;
		color: #409EFF;
		font-weight: bold;
	}
</style>



