<template>
	<div id="login">
		<div class="title">
			<p>用户登录</p>
		</div>
		<div>
			<p class="lable">用户名</p>
			<el-input v-model="emp.ipone" placeholder="请输入电话号码"></el-input>
			<p class="lable">密码</p>
			<el-input v-model="emp.password" show-password placeholder="请输入密码"></el-input>
		</div>
		<div class="btn">
			 <el-button type="primary" style="width:100%;" @click="userLogin">登  录</el-button>
		</div>
	</div>
</template>

<script >
	import { mapMutations } from 'vuex'
	export default {
		data(){
			return {
				emp:{
					ipone:"",
					password:"",
				}
			}
		},
		methods:{
			...mapMutations(['changeuserId','changuserIphone','changeuserName','changeuserType']),
			userLogin(){
				this.$http.post("empServe",{
					empList:JSON.stringify(this.emp),
					method:"login"
				}).then(res=>{
					console.log(res)
					if (res.code) {//code有值，说明存在问题
						this.$message({
				          message: res.msg,
				          type: 'warning'
				        });
					} else {
							
						if (res.dataVo.type == 1) {//管理员
						
							this.changeuserName(res.dataVo.name);
							this.changuserIphone(res.dataVo.iphone);
							this.changeuserType(res.dataVo.type);
							this.changeuserId(res.dataVo.id);
							//跳转到登录前的页面
							let redirect = decodeURIComponent(this.$route.query.redirect || '/admin/index');
							this.$router.push({//你需要接受路由的参数再跳转
								path: redirect
							}); 
							
						}
					}
				})
			}
		}
	}
</script>

<style scoped>
	#login{
		width: 300px;margin: 200px auto;
	}
	.title{
		text-align: center;
		font-size: 30px;
		color: #409EFF;
	}
	.lable{
	
	}
	.btn{
		margin-top: 30px;
		width: 100%;
	}
</style>



