<template>
	<div id="sysConfig">
		<div class="m-l">
			<div>
				<h3>上班时间：{{tims.toWorkTime}}</h3>
				<h3>下班时间：{{tims.downWorkTime}}</h3>
				<hr>
			</div>
			<div>
				<div>
					<el-form label-position="top" label-width="100px" :model="tims">
					  <el-form-item label="上班时间">
					    <el-col :span="11">
					      <el-time-select v-model="tims.toWorkTime" :picker-options="startObj" placeholder="选择时间" style="width:300px;"></el-time-select>
					    </el-col>
					  </el-form-item>
			
					  <el-form-item label="下班时间">
					    <el-col :span="11">
					      <el-time-select v-model="tims.downWorkTime" :picker-options="endObj" placeholder="选择时间"  style="width:300px;"></el-time-select>
					    </el-col>
					  </el-form-item>
			
					  <el-form-item>
					    <el-button type="primary" @click="updateTime">保存修改</el-button>
					  </el-form-item>
					</el-form>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data () {
			return {
				tims:{
					id:"",
					toWorkTime:"",
					downWorkTime:""
				},
				startObj:{
					start: '08:00',
				    step: '00:10',
				    end: '09:30'
				},
				endObj:{
					start: '17:00',
				    step: '00:10',
				    end: '18:30'
				}
			}
		},
		mounted(){
			this.quarySysTime()
		},
		methods:{
			submitForm(){
				 this.$message({
					  message: '你已到达宗师境界',
					  type: 'success'
				});
			},
			//查询考勤信息
			quarySysTime(){
				this.$http.get("comConfigUI").then(res =>{
					//{cName: "磐石", downWorkTime: "18:30:00", id: 1, toWorkTime: "09:30:00"}
						this.tims =res.dataVo[0]
					})
			},
			//修改上下班时间  
			updateTime(){
				this.$http.get("comConfigUI",{
					method:"uptateTime",
					configList:this.tims
				}).then(res =>{
					let _this = this
					if(res.code){
						this.$message({
						  message: res.msg,
						  type: 'error',
						});
					}else{
						this.$message({
						  message: res.msg,
						  type: 'success',
						  onClose:()=>{
							  _this.quarySysTime();
						  }
						});
					}
				})
			}
		}
	}
</script>

<style scoped>
	.m-l{
		margin-left: 20px;
	}
</style>



