<template>
	<div id="admin">
		<el-container style="height: 85%; border: 1px solid #eee"  >
			<el-aside width="200px" style="background-color: rgb(238, 241, 246) ;position: relative;top: 88px;">
				<el-menu :default-openeds="[activeIndex]"   :router=true>
					
					<el-menu-item index="/admin/index">
						<i class="el-icon-menu"></i>首页
					</el-menu-item>
					<el-submenu index="2">
						<template slot="title">
							<i class="el-icon-menu"></i>
							员工中心
						</template>
						<el-menu-item-group>
							<el-menu-item index="/admin/empList" >员工列表</el-menu-item>
							<el-menu-item index="/admin/addEmp">员工增加</el-menu-item>
						</el-menu-item-group>
					</el-submenu>
					<el-menu-item index="/admin/detpManage"><i class="el-icon-menu"></i>部门管理</el-menu-item>
					<el-submenu index="3">
						<template slot="title">
							<i class="el-icon-setting"></i>考勤管理
						</template>
						<el-menu-item-group>
							<el-menu-item index="/admin/checking" >考勤查询</el-menu-item>
							<el-menu-item index="/admin/systConfig">考勤设置</el-menu-item>
						</el-menu-item-group>
					</el-submenu>
				</el-menu>
			</el-aside>

			<el-container >
				<el-header style="height: 99px;">
					<h3 style="text-align: center;position: relative;top: 18px;">管理员主页</h3>
					<div style="text-align: right;  ">
						<h4 style="margin-right: 50px;">欢迎 {{userName}}</h4>
						<el-dropdown>
							<i class="el-icon-setting" style="margin-right: 15px;font-size: 12px">
								<el-button type="text" @click="outLogin">退出登录</el-button>
							</i>
						</el-dropdown>
					</div>
				</el-header>
				
				<!-- 将路由的信息内容展示到这里 -->
				<router-view> </router-view>
				 
			</el-container>
		</el-container>
	</div>
</template>

<script>
	// 在单独构建的版本中辅助函数为 Vuex.mapState
	import { mapState,mapMutations } from 'vuex'
	
	export default {
		 data() {
	      return {
	        activeIndex: '/admin/index',
	      };
	    }, 
	    computed:{
	    	...mapState(['userName'])
	    },
	    methods: {
		  ...mapMutations(['clearUserInfo']),
		  outLogin(){
		  	this.clearUserInfo();
		  	this.$router.replace({
		  		path:"/login"
		  	})
		  }
	    }
	}
</script>

<style scoped>
	 .el-header {
		background-color: #B3C0D1;
		color: #333;
		line-height: 15%;
	  }
	
	  .el-aside {
		color: #333;
	  }
</style>



